@import "./src/sass/helpers/var";


.social-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 30%;
  margin: 0 auto;

  &__item {
    width: 100%; }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2.5rem;
    height: 2.5rem;

    svg {
      fill: rgba($purple, 0.75);
      transition: $transition; }

    &:hover {
      svg {
        fill: rgba($purple, 1);
        transform: scale(1.05); } } } }
