html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.about {
  width: 40%;
  display: flex;
  background-color: rgba(249, 249, 249, 0.5);
  height: 100vh;
}

.about--wrapper {
  display: flex;
  flex-direction: column;
  padding: 12.5% 10%;
  justify-content: center;
  align-items: flex-start;
}

.about__title {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 3.5rem;
  padding: 10% 0 5%;
}

.about__description--wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 5% 0;
}

.about__description--wrapper p {
  padding: 0.75rem 0;
  font-size: 1.25rem;
  color: #ffffff;
}

.blog-box {
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding: 2.5%;
  flex-wrap: wrap;
}

.blog-box__item {
  display: flex;
  flex-direction: column;
  padding: 5%;
  background-color: #ffffff;
  transition: all 0.3s ease;
  width: calc(100% / 3);
  border: 1px solid #7d7d7d;
}

.blog-box__item h3 {
  font-size: 2rem;
  width: 100%;
  text-align: left;
}

.blog-box__item p {
  width: 100%;
  text-align: justify;
  font-size: 1rem;
  padding: 1.25rem 0;
}

.blog-box__item:nth-child(5n) {
  background-color: orange;
  width: calc(100% / 3 * 2);
}

.blog-box__item:nth-child(6n) {
  background-color: rebeccapurple;
  width: calc(100% / 3 * 2);
}

.blog-box__item:nth-child(10n) {
  background-color: #FF0043;
  width: calc(100% / 3);
}

.blog-box__item:nth-child(13n) {
  background-color: #fbd46d;
  width: calc(100% / 3 * 2);
}

.blog-box__item:nth-child(15n) {
  background-color: #4f8a8b;
  width: calc(100% / 3);
}

.blog-box__item:nth-child(18n) {
  background-color: #c629ad;
  width: calc(100% / 3);
}

.blog-box__item:nth-child(19n) {
  background-color: #aaddff;
  width: calc(100% / 3 * 2);
}

.blog-box__item:nth-child(20n) {
  background-color: #aaddff;
  width: calc(100% / 3 * 2);
}

.blog-box__item:nth-child(24n) {
  background-color: indianred;
  width: calc(100% / 3);
}

.blog-box__item:nth-child(25n) {
  background-color: indianred;
  width: calc(100% / 3);
}

.blog-box__item:nth-child(27n) {
  background-color: indianred;
  width: calc(100% / 3 * 2);
}

.blog-box__item:hover, .blog-box__item:focus, .blog-box__item:active {
  box-shadow: 0 0 30px 0 rgba(178, 69, 222, 0.5);
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border: none;
  transition: all 0.3s ease;
}

.btn:hover, .btn:focus, .btn:active {
  cursor: pointer;
}

.btn--submit {
  font-size: 1.5rem;
  width: 40%;
  padding: 0.5rem 1rem;
  background-color: white;
  border-radius: 7.5px;
}

.btn--submit:hover, .btn--submit:focus, .btn--submit:active {
  background-color: rgba(230, 92, 0, 0.85);
  color: #ffffff;
}

.btn--close {
  position: absolute;
  width: 5rem;
  height: 5rem;
  color: #ffffff;
  background-color: transparent;
  border: none;
  font-size: 3.5rem;
  line-height: 4rem;
  top: 10%;
  right: 12.5%;
}

.btn--close:hover, .btn--close:focus, .btn--close:active {
  cursor: pointer;
}

.btn--order {
  width: 20%;
  font-size: 1rem;
  background-color: rgba(178, 69, 222, 0.75);
  padding: 1rem 1.5rem;
  color: #ffffff;
  border-radius: 5px;
}

.btn--order:hover, .btn--order:focus, .btn--order:active {
  background-color: #b245de;
}

.footer {
  width: 100%;
  margin: 0 auto;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer--wrapper {
  padding: 1.5rem 2.5%;
  background-color: rgba(0, 0, 0, 0.15);
  width: 60%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 11;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.footer--about {
  width: 100%;
  background-color: transparent;
  justify-content: flex-end;
}

.footer--about .social-menu {
  width: 15%;
  margin: 0;
}

.footer--about .btn--order {
  width: 15%;
}

.footer--about .copyright {
  margin-right: auto;
  color: #ffffff;
}

.footer--about .copyright--color {
  padding-left: 1rem;
  color: #000000;
}

.copyright {
  width: 20%;
  display: flex;
}

.header {
  display: flex;
  width: 100%;
  min-height: 5rem;
  box-shadow: 0 0 5px 0 rgba(178, 69, 222, 0.5);
  background-color: #ffffff;
  z-index: 999;
  position: fixed;
}

.header--background {
  background-color: transparent;
  border: none;
}

.link--wrapper {
  display: flex;
  width: 100%;
  padding: 1rem 0;
  justify-content: flex-start;
  align-items: center;
}

.link__icon {
  display: flex;
  width: 2.5rem;
  height: 2.5rem;
  background-image: url("../img/icon-pdf.png");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.link__item {
  display: flex;
  padding: 0 1rem;
  width: 30%;
  font-size: 1.25rem;
  color: #ffffff;
}

.link__item:hover {
  color: #000000;
}

.link--color {
  color: #000000;
}

.modal {
  display: flex;
  flex-direction: column;
  width: 40%;
  padding: 3.5% 5%;
  background-color: rgba(178, 69, 222, 0.75);
  position: relative;
}

.modal-order {
  z-index: 9999;
  display: flex;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(178, 69, 222, 0.25);
  justify-content: center;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  transform: translateX(100%);
  transition: all 1s ease-in-out;
}

.modal__title {
  font-size: 3.25rem;
  width: 100%;
  color: #ffffff;
}

.modal__description {
  font-size: 1.25rem;
  padding: 1.5rem 0;
  width: 100%;
  color: #f9f9f9;
}

.modal__input {
  border-radius: 7.5px;
  border: none;
  margin: 0.75rem 0;
  padding: 1rem 1rem;
  font-size: 1.35rem;
  width: 100%;
}

.modal__comment {
  margin: 0.75rem 0;
  border-radius: 7.5px;
  padding: 1.25rem 1rem;
  font-size: 1.25rem;
  border: none;
}

.modal-order--active {
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
}

.input--wrapper {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
}

.checkbox:hover, .checkbox:focus, .checkbox:active {
  cursor: pointer;
}

label {
  color: #ffffff;
  margin: 0 10px;
}

label:hover, label:focus, label:active {
  cursor: pointer;
}

.navigation {
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}

.navigation__wrapper {
  display: flex;
  justify-self: flex-end;
  justify-content: space-between;
  width: 45%;
  align-items: center;
}

.navigation__item {
  width: 100%;
  height: 100%;
  text-align: center;
  position: relative;
}

.navigation__link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  width: 100%;
  padding: 0.75rem 1rem;
  font-size: 1.15rem;
  color: #cccccc;
}

.navigation__link--color {
  color: #000000;
}

.navigation__link:after {
  content: '';
  display: block;
  width: 50%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  transform: translateX(50%);
  background-color: #b245de;
  visibility: hidden;
  opacity: 0;
}

.navigation__link:hover, .navigation__link:focus, .navigation__link:active {
  color: #000000;
}

.navigation__link:hover:after, .navigation__link:focus:after, .navigation__link:active:after {
  visibility: visible;
  opacity: 1;
}

.navigation__link:hover ~ .nav-submenu, .navigation__link:focus ~ .nav-submenu, .navigation__link:active ~ .nav-submenu {
  transform: translate(-10%, 0) scale(1);
  opacity: 1;
  visibility: visible;
}

.nav-submenu {
  display: flex;
  opacity: 0;
  visibility: hidden;
  transform-origin: bottom center;
  transform: translate(-10%, -100%) scale(0);
  position: absolute;
  top: 145%;
  left: 0;
  flex-direction: column;
  background-color: #ffffff;
  box-shadow: inset 0 0 2.5px 1px rgba(178, 69, 222, 0.5);
  padding: 1rem 0;
  z-index: 998;
  transition: all 1s ease;
}

.nav-submenu__item {
  display: flex;
}

.nav-submenu__link {
  display: flex;
  font-weight: 400;
  width: 100%;
  font-size: 1.15rem;
  padding: 0.75rem 2.5rem;
  justify-content: flex-start;
  align-items: center;
}

.nav-submenu__link:hover, .nav-submenu__link:active, .nav-submenu__link:focus {
  color: rgba(178, 69, 222, 0.85);
}

.nav-submenu__link--color {
  color: #000000;
}

.logo {
  width: 15%;
  padding: 0.5rem 0;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.logo svg {
  fill: rgba(178, 69, 222, 0.75);
  height: 2rem;
}

.our-projects {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 2.5% 20%;
}

.our-projects__title {
  font-size: 3.75rem;
  width: 100%;
}

.our-projects--wrapper {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2.5%;
  padding: 2rem 0;
}

.pages {
  display: flex;
  width: 100%;
  height: 100vh;
  justify-content: space-between;
  align-items: center;
  padding: 12.5rem 10% 12.5rem 27.5%;
}

.pages--wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  height: 50%;
}

.pages__number {
  display: flex;
  width: 35%;
  justify-content: flex-start;
  align-items: center;
  text-align: left;
  font-size: 5rem;
  color: #b245de;
  transition: all 0.3s ease;
  position: relative;
}

.pages__number:before {
  display: block;
  width: 1.5rem;
  content: '';
  height: 1.5rem;
  position: absolute;
  top: 50%;
  left: -65%;
  transform: translateY(-1px);
  border-radius: 50%;
  background-color: #fbd46d;
  z-index: 88;
}

.pages__title {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 5.5rem;
}

.pages__description {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 1.5rem;
  line-height: 2.5rem;
  padding: 0 5% 0 15%;
  position: relative;
}

.pages__description:before {
  content: '\1433';
  font-size: 2.5rem;
  display: block;
  width: 7.5%;
  height: 50%;
  position: absolute;
  top: 50%;
  left: 3.5%;
  transition: all 0.3s ease;
  transform: translate(0, -50%);
}

.pages__description:hover:before {
  color: #fbd46d;
}

.pages__img {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pages__img img {
  object-fit: contain;
  width: 80%;
}

.project {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.project__image {
  width: 100%;
  object-fit: cover;
  background-color: #ffffff;
  transition: all 0.3s ease;
}

.project__image:hover, .project__image:focus, .project__image:active {
  background-color: rgba(178, 69, 222, 0.5);
}

.project__image img {
  object-fit: cover;
  width: 100%;
}

.project__title {
  font-size: 2rem;
  width: 100%;
  padding: 2rem 0;
  transition: all 0.3s ease;
}

.project__caption {
  display: flex;
  margin-top: 2.5rem;
  width: 100%;
  transition: all 0.3s ease;
}

.project__caption:hover, .project__caption:focus, .project__caption:active {
  color: #b245de;
}

.services {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  padding: 2.5% 0;
}

.services__title {
  font-size: 3.5rem;
  padding: 0 2.5rem;
  width: 45%;
}

.services__description {
  width: 100%;
  padding: 1.5rem 2.5rem 0;
  text-align: left;
  font-size: 1.25rem;
}

.services__caption {
  width: 100%;
  color: #7d7d7d;
  font-size: 1.5rem;
}

.services-box {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
  padding: 2.5%;
  grid-gap: 2.5%;
}

.services-box__item {
  display: flex;
  flex-direction: column;
  padding: 10%;
  background-color: #ffffff;
  transition: all 0.3s ease;
}

.services-box__item h3 {
  font-size: 2rem;
  width: 100%;
  text-align: left;
}

.services-box__item p {
  width: 100%;
  text-align: justify;
  font-size: 1rem;
  padding: 1.25rem 0;
}

.services-box__item:hover, .services-box__item:focus, .services-box__item:active {
  box-shadow: 0 0 30px 0 rgba(178, 69, 222, 0.5);
}

.social-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 30%;
  margin: 0 auto;
}

.social-menu__item {
  width: 100%;
}

.social-menu__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
}

.social-menu__link svg {
  fill: rgba(178, 69, 222, 0.75);
  transition: all 0.3s ease;
}

.social-menu__link:hover svg {
  fill: #b245de;
  transform: scale(1.05);
}

.spinner {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.spinner--wrapper {
  border: 2px dashed #b245de;
  position: absolute;
  height: 85vh;
  width: 42.5%;
  left: 0;
  transform: translateX(-50%);
  margin: auto 0;
  border-radius: 50%;
}

.tabs {
  width: 100%;
  padding: 3.5% 0;
}

.tabs-triggers {
  display: flex;
  width: 100%;
  margin: 0 auto;
  padding: 1rem 20%;
  border-bottom: 1px solid rgba(178, 69, 222, 0.5);
  align-items: center;
  justify-content: flex-start;
}

.tabs-triggers__item {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.15rem;
  padding: 1rem 0;
  text-align: center;
  color: black;
  letter-spacing: 0;
  flex-grow: 1;
}

.tabs-triggers__item:hover, .tabs-triggers__item:focus, .tabs-triggers__item:active {
  background-color: rebeccapurple;
  color: #ffffff;
}

.tabs-content {
  display: flex;
}

.tabs-content__item {
  display: none;
}

.tabs-content__item--active {
  display: flex;
  animation: show 0.75s ease-in-out forwards;
}

.page {
  width: 100%;
  position: relative;
  min-height: 100vh;
}

.page--about {
  display: flex;
  justify-content: flex-end;
  background-image: url("../img/pexels-photo-5474031.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.page--contacts {
  display: flex;
  justify-content: flex-end;
  background-color: #ffffff;
}

.section {
  display: flex;
  align-items: center;
  width: 100vw;
  min-height: 100vh;
  position: absolute;
  transform-origin: left center;
  transition: all 1.5s ease;
  z-index: 3;
  opacity: 0.25;
}

.section--active {
  z-index: 5;
  opacity: 1;
}

.section--active .pages {
  padding-left: 27.5%;
}

.section--active .pages__number {
  transition: all 0.3s ease;
  color: #000000;
}

.section--active .pages__number:before {
  left: -67.5%;
}

.section--wrapper {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
}

*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Raleway;
  font-weight: 500;
  color: #000000;
  height: 100vh;
  display: flex;
  background-color: #f9f9f9;
  flex-direction: column;
  justify-content: space-between;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

.main-content {
  position: relative;
  width: 100%;
  padding-bottom: 5%;
}

.main--padding {
  padding: 0;
}

article {
  width: 100%;
  position: relative;
}

svg {
  width: 100%;
  height: 100%;
}

a {
  text-decoration: none;
  transition: all 0.3s ease;
  width: 100%;
  color: #000000;
}

picture {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

p {
  line-height: 1.5rem;
  color: #7d7d7d;
}

.container {
  width: 100%;
  height: 100%;
  display: flex;
}

.scroll--hidden {
  overflow: hidden;
}

.rotate {
  transform: translateX(20px);
}

.container {
  display: flex;
  width: 65%;
  margin: 0 auto;
  flex-direction: column;
}

@keyframes show {
  from {
    opacity: 0;
    transform: translateX(-50%);
    visibility: hidden;
  }
  to {
    opacity: 1;
    transform: translateX(0);
    visibility: visible;
  }
}

@keyframes showPage {
  10% {
    transform: scale(1, 0.002);
  }
  35% {
    transform: scale(0.2, 0.002);
    opacity: 1;
  }
  50% {
    transform: scale(0.2, 0.002);
    opacity: 0;
  }
  85% {
    transform: scale(1, 0.002);
    opacity: 1;
  }
  100% {
    transform: scale(1, 1);
  }
}

.title--wrapper {
  display: flex;
  width: 100%;
  justify-content: flex-start;
  align-items: center;
}

.map {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
}

.map--wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 1;
}

.contacts {
  z-index: 5;
  background-color: rgba(255, 255, 255, 0.65);
}

.contacts a,
.contacts p {
  color: #000000;
}

