@import "./src/sass/helpers/var";

.footer {
  width: 100%;
  margin: 0 auto;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &--wrapper {
    padding: 1.5rem 2.5%;
    background-color: rgba($black, 0.15);
    width: 60%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 11;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px; }

  &--about {
    width: 100%;
    background-color: transparent;
    justify-content: flex-end;

    .social-menu {
      width: 15%;
      margin: 0; }

    .btn--order {
      width: 15%; }

    .copyright {
      margin-right: auto;
      color: $white;
      &--color {
        padding-left: 1rem;
        color: $black; } } } }

.copyright {
  width: 20%;
  display: flex; }
