@import "./src/sass/helpers/var";

.tabs {
  width: 100%;
  padding: 3.5% 0; }

.tabs-triggers {
  display: flex;
  width: 100%;
  margin: 0 auto;
  padding: 1rem 20%;
  border-bottom: 1px solid rgba($purple, 0.5);
  align-items: center;
  justify-content: flex-start;

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.15rem;
    padding: 1rem 0;
    text-align: center;
    color: black;
    letter-spacing: 0;
    flex-grow: 1;

    &:hover,
    &:focus,
    &:active {
      background-color: rebeccapurple;
      color: $white; } } }

.tabs-content {
  display: flex;

  &__item {
    display: none;

    &--active {
      display: flex;
      animation: show 0.75s ease-in-out forwards; } } }
