@import "helpers/reset";
@import "helpers/mixins";
@import "res/about";
@import "res/blog-box";
@import "res/btn";
@import "res/footer";
@import "res/header";
@import "res/link";
@import "res/modal";
@import "res/nav";
@import "res/our-projects";
@import "res/pages";
@import "res/project";
@import "res/services";
@import "res/services-box";
@import "res/social-menu";
@import "res/spinner";
@import "res/tabs";
@import "res/page";
@import "res/section";

*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: Raleway;
  font-weight: 500;
  color: $black;
  height: 100vh;
  display: flex;
  background-color: $gray;
  flex-direction: column;
  justify-content: space-between;
  overflow-x: hidden;
  scroll-behavior: smooth; }

.main {
  &-content {
    position: relative;
    width: 100%;
    padding-bottom: 5%; }

  &--padding {
    padding: 0; } }

article {
  width: 100%;
  position: relative; }

svg {
  width: 100%;
  height: 100%; }

a {
  text-decoration: none;
  transition: $transition;
  width: 100%;
  color: $black; }

picture {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

p {
  line-height: 1.5rem;
  color: $darkgray; }


.container {
  width: 100%;
  height: 100%;
  display: flex; }



.scroll--hidden {
  overflow: hidden; }

.rotate {
  transform: translateX(20px); }

.container {
  display: flex;
  width: 65%;
  margin: 0 auto;
  flex-direction: column; }

@keyframes show {
  from {
    opacity: 0;
    transform: translateX(-50%);
    visibility: hidden; }
  to {
    opacity: 1;
    transform: translateX(0);
    visibility: visible; } }

@keyframes showPage {
  10% {
    transform: scale(1, 0.002); }
  35% {
    transform: scale(0.2, 0.002);
    opacity: 1; }
  50% {
    transform: scale(0.2, 0.002);
    opacity: 0; }
  85% {
    transform: scale(1, 0.002);
    opacity: 1; }
  100% {
    transform: scale(1, 1); } }

.title {
  &--wrapper {
    display: flex;
    width: 100%;
    justify-content: flex-start;
    align-items: center; } }


.map {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  &--wrapper {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 1; } }

.contacts {
  z-index: 5;
  background-color: rgba($white, 0.65);
  a,
  p {
    color: $black; } }




@import "helpers/fonts";
